/* AdminMenu.css */

/* Container Style */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #3b5998; /* Updated background color */
  padding: 10px 20px;
}

/* Navbar Links Style */
.navbar-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex: 1;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  padding: 8px 12px;
  transition: color 0.3s ease;
}

.navbar-links a:hover {
  color: #ddd;
}

.navbar-user {
  display: flex;
  align-items: center;
}

.navbar-user span {
  margin-right: 10px;
}

.navbar-user button {
  background: #27ae60; /* Pleasant green color */
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.navbar-user button:hover {
  background: #219653; /* Darker green color on hover */
}

/* Logo Style */
.navbar-logo {
  display: flex;
  align-items: center;
}

.navbar-logo img {
  height: 60px; /* Increase logo size */
  margin-right: 20px;
}
